@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


:root {
  --color-cream: hsl(30, 38%, 92%);
  --color-white: hsl(0, 0%, 100%);
  --color-verydarkblue: hsl(212, 21%, 14%);
  --color-darkgrayishblue: hsl(228, 12%, 48%);
  --color-darkcyan: hsl(158, 36%, 37%);
  --color-darkcyanhover: hsl(159, 40%, 14%);
}

* {
    -webkit-user-select: none;
            user-select: none;
}
.main {
  background-color: hsl(30, 38%, 92%);
  background-color: var(--color-cream);
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.card {
  background-color: hsl(0, 0%, 100%);
  background-color: var(--color-white);
  display: flex;
  width: 500px;
  height: 50vh;
  position: relative;
  border-radius: 10px;
}
.left {
  width: 50%;
}
.right {
  width: 50%;
  object-fit: contain;
  padding: 30px;
  box-sizing: border-box;
}
.mainImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  pointer-events: none;
}
.header {
  letter-spacing: 0.5ch;
  font-size: small;
  font-weight: 600;
  color: hsl(228, 12%, 48%);
  color: var(--color-darkgrayishblue);
}
.title {
  font-family: Fraunces, "Times New Roman", Times, serif;
  margin-top: 15px;
  color: hsl(212, 21%, 14%);
  color: var(--color-verydarkblue);
  font-size: 1.7rem;
  line-height: 28px;
}
.description {
  margin-top: 15px;
  font-size: 0.75rem;
  font-weight: 600;
  color: hsl(228, 12%, 48%);
  color: var(--color-darkgrayishblue);
  line-height: 20px;
}
.prices {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}
.price {
  font-family: Fraunces, "Times New Roman", Times, serif;
  font-size: 26px;
  color: hsl(158, 36%, 37%);
  color: var(--color-darkcyan);
}
.prevprice {
  margin-top: 8px;
  margin-right: 20px;
  font-size: small;
  color: hsl(228, 12%, 48%);
  text-decoration: line-through;
  font-weight: 600;
}
.iconCart {
  width: 12px;
  margin-right: 10px;
  pointer-events: none;
}
.btnAddToCart {
  transition: ease 0.1s;
  border-radius: 10px;
  background-color: hsl(158, 36%, 37%);
  background-color: var(--color-darkcyan);
  color: hsl(0, 0%, 100%);
  color: var(--color-white);
  justify-content: center;
  margin-top: 20px;
  text-align: center;
  font-size: 13px;
  font-weight: 600;
  padding: 12.5px;
  vertical-align: middle;
  display: flex;
  cursor: pointer;
}
.btnAddToCart:hover {
  background-color: hsl(159, 40%, 14%);
  background-color: var(--color-darkcyanhover);
}

